
//
//GENERAL styles for homepage Blocks
//


//fix needed for firefox and safari
.block{
	position:relative;
}
#block-views-blog-latest-news,#block-views-trials-offers-renewals,#block-views-blog-connect-newsletter-block,#block-boxes-partner-libraries-home,#block-views-blog-mobile-news-letter{
	margin-top:40px;
	.more-link{
		text-align: left;
	}
	h2.block__title:before{
		background: url('../images/block-header-bg-before.png') no-repeat scroll top left;
		width: 22px;
		height: 64px;
		content: '';
	    display: block;
		position:absolute;
		top:0px;
		left:-22px;
	}
	h2.block__title:after{
		background: url('../images/block-header-bg-after.png') no-repeat scroll top left;
		width: 26px;
		height: 64px;
		content: '';
	    display: block;
		position:absolute;
		top:0px;
		right:-26px;
	};
	h2.block__title{
		background: url('../images/block-head-repeat.png') top left;
		background-repeat: repeat-x;
		width: 82%;
		height: 64px;
		font-size:$fz-h3;
		padding:0 15px ;
		padding-top:25px;
		margin-bottom:0px;
		text-transform:uppercase;
		font-weight:$fw-semi-bold;
		position: absolute;
	    top: -25px;
		left: 22px;
	}
	.block__content{
		margin-bottom:35px;
		.view-content{
			margin-bottom:50px;
		}
	}
	.block__content > div{
		padding-left:23px;
		padding-right:33px;
		background:$grey-light;		
		border-bottom:2px solid $orange;
		padding-top:70px;
		padding-bottom:20px;
		
	}
}
//verriders
#block-boxes-partner-libraries-home{
	margin-top: 70px;
	@include grid-span(12,1);
	.boxes-box-content{
		a{
			font-weight:$fw-semi-bold;
			color:$violet-dark;
		}
		h2:before{
			background: url('../images/block-header-bg-before.png') no-repeat scroll top left;
			width: 22px;
			height: 64px;
			content: '';
		    display: block;
			position:absolute;
			top:0px;
			left:-22px;
		}
		h2:after{
			background: url('../images/block-header-bg-after.png') no-repeat scroll top left;
			width: 26px;
			height: 64px;
			content: '';
		    display: block;
			position:absolute;
			top:0px;
			right:-26px;
		};
		h2{
			background: url('../images/block-head-repeat.png') top left;
			background-repeat: repeat-x;
			height: 64px;
			font-size:$fz-h3;
			padding:0 15px ;
			padding-top:25px;
			margin-bottom:0px;
			text-transform:uppercase;
			font-weight:$fw-semi-bold;
			position: absolute;
		    top: -25px;
			left:22px;
			width: 84%;
		}
		@include breakpoint($tablet-layout){
			h2{
				width: 92%;
			}
		}
		@include breakpoint($desktop-layout){
			h2{
				width: 95%;
			}
		}
	}
}
.view-organizations{
	@include grid-span(12,1);
	margin-bottom:50px;
	.view-content {
		 .views-row{
			 float: left;
			 margin-right:5px;
		 }
	}
}
#boxes-box-partner_libraries_home.boxes-box{
	border:none !important;
	background:$white !important;
}
#block-boxes-partner-libraries-home .block__content {
	margin-bottom: 15px;
}
#block-views-blog-connect-newsletter-block{
	.block__content{
		.view-content{
			margin-bottom:30px;
		}
		.field--name-body{
				margin-bottom:50px;
		}
	}
}
//
//footer Blocks
//
#boxes-box-newsletter .boxes-box-content{
	background:$dark-cyan;
	text-align: center;
	a{
	    display: inline-block;
		padding:10px;
	}
	.news-letter-icon{
		@include fontello-icon('\e802') {
		   font-size: 1.0em;
		   cursor: pointer;
		   color:$orange;
		   position:relative;
		   margin-right:10px;
		 }
	}
	.news-letter-text{
		float:right;
		color:$white;
		font-size:$fz-h2;
		font-weight:$fw-semi-bold;
	}
}